import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { isEmpty } from "lodash"

import { selectCkm } from 'features/ckm/ckmSlice'

import TopicItem from '../TopicItem'
import './Topic.css';

function Topic() {
    const ckm = useSelector(selectCkm)

    useEffect(() => {

    }, [])

    if (isEmpty(ckm.topics)) return null

    return (
        <>
        {Object.keys(ckm.topics).map(topicKey => (
            <div key={topicKey} className="topicList">
                <button className="topicGroup">{topicKey}</button>
                {Object.keys(ckm.topics[topicKey]).map(itemKey => (
                    <TopicItem key={itemKey} itemId={itemKey} data={ckm.topics[topicKey][itemKey]} />
                ))}
            </div>
        ))}
        </>
    )
}

export default Topic
