import { createSlice } from '@reduxjs/toolkit';

export const slice = createSlice({
  name: 'ckm',
  initialState: {
    topics: [],
    hedData: [],
    playBook: {},
  },
  reducers: {    
    setTopics: (state, action) => {
      state.topics = action.payload;
    },
    setHedData: (state, action) => {
      state.hedData = action.payload;
    },
    setPlayBook: (state, action) => {
      state.playBook = action.payload;
    },
    setTopicSelected: (state, action) => {
      Object.keys(state.topics).forEach(topicKey => {
        Object.keys(state.topics[topicKey]).forEach(itemKey => {
          if (itemKey === action.payload) {
            const item = state.topics[topicKey][itemKey]
            item.selected = !item.selected
          }
        })
        
      })

    },
  },
});

export const { setTopics, setHedData, setPlayBook, setTopicSelected } = slice.actions;

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched
export const incrementAsync = amount => dispatch => {
  setTimeout(() => {
    // dispatch(incrementByAmount(amount));
  }, 1000);
};

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state) => state.counter.value)`
export const selectCkm = state => state.ckm;

export default slice.reducer;
