import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import * as d3 from "d3"

import { selectCkm } from 'features/ckm/ckmSlice'
import { getX, getY, addParentNode, isLeft, getPaths } from './utility'
import { isEmpty, cloneDeep } from "lodash"

import './Hed.css'

function displayHed(data, receiveClickFromD3Element) {
    const diameter = 750;
    const padding = 180;
    const radius = diameter / 2 - padding;

    //clean up
    d3.select("svg").select('g').remove();
    if (isEmpty(data)) return;

    const svg = d3.select("svg")
        .attr("width", diameter)
        .attr("height", diameter);

    const stratify = d3.stratify()
        .id((d) => d.name);

    const cluster = d3.cluster()
        .size([Math.PI * 2, radius]);

    const line = d3.line()
        .x((d) => getX(d))
        .y((d) => getY(d))
        .curve(d3.curveBasis);
    //.curve(d3.curveBundle);


    //  d3.json("./assets/hierarchy.json").then(function (data: any) {
    addParentNode(data);
    let root = stratify(data);
    cluster(root);
    let leaves = root.leaves();

    let g = svg.append("g")
        .attr("transform", "translate(" + [diameter / 2, diameter / 2] + ")");

    let node = g.append("g")
        .selectAll("text")
        .data(leaves)
        .enter().append("text")
        .attr("transform", (d) => "translate(" + [getX(d), getY(d)] + ") " + //if not translate, rotate will behave strange
            "rotate(" + (d.x * 180 / Math.PI - (isLeft(d) ? 180 : 0)) + ")")
        .attr("text-anchor", (d) => isLeft(d) ? "end" : "start")
        .attr("dx", (d) => isLeft(d) ? "-0.7em" : "0.7em")
        .attr("dy", "0.3em")
        .text(d => d.data.shortName)
        .style('fill', function (d) {
            if ((d.data.name).includes('|PRODUCT_PHASE|'))
                return 'blue';
            else if ((d.data.name).includes('|ASSET|'))
                return 'green';
            else if ((d.data.name).includes('|PLAYBOOK|'))
                return 'orange';
            /*
            else if((d.data.name).includes('|Metrics|'))
              return 'purple';
            else if((d.data.name).includes('|Qualifier|'))
              return 'black';
            */
            else
                return 'darkBlue';
        })
        .on("mouseover", function (d) {
            //console.log('>>> node')
            //console.log(node)
            //console.log('>>> link')
            //console.log(link)
            node.each(n => { n.target = n.source = false; });
            link.classed("link--target", l => { if (l.target === d) return l.source.source = true; })
                .classed("link--source", l => { if (l.source === d) return l.target.target = true; })
                .filter(l => l.target === d || l.source === d)
                .raise();
            node.classed("node--source", n => n.source)
                .classed("node--target", n => n.target)
            //console.log(d3.selectAll(".link--target"));
        })
        .on("mouseout", function (d) {
            link.classed("link--source", false)
                .classed("link--target", false);
            node.classed("node--source", false)
                .classed("node--target", false);
        })
        .on("click", function (d) {
            const selectedClick = d3.select(this)
            receiveClickFromD3Element(selectedClick, d)
        });
        
    let link = g.append("g")
        .selectAll("path")
        .data(getPaths(leaves))
        .enter().append("path")
        .each(d => { d.source = d[0]; d.target = d[d.length - 1]; })
        .attr("d", line);
    //});
}

function Hed() {
    const ckm = useSelector(selectCkm)
    const receiveClickFromD3Element = (selected, datum) => {
        console.log(selected)
        console.log(datum)
    }

    useEffect(() => {
        if (!isEmpty(ckm.hedData)) displayHed(cloneDeep(ckm.hedData), receiveClickFromD3Element)
    }, [ckm.hedData])

    return (
        <div className="div-svg">
            <svg></svg>
        </div>
    )
}

export default Hed
