import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import * as d3 from "d3"
import { isEmpty, cloneDeep } from "lodash"

import { setPlayBook } from 'features/ckm/ckmSlice'
import { playbooks } from 'component/Hed/store/data'
import plays from 'component/Hed/store/play'
import { selectCkm, setTopicSelected } from 'features/ckm/ckmSlice'

import './TopicItem.css';

async function addPlays(dispatch, selectedProductPhases, assets, plays) {
    const filteredPlays = {}
    selectedProductPhases.forEach(pp => {
        assets.forEach(as => {
            const lookupKey = `${pp} <> ${as}`
            const play = plays[lookupKey]
            if (play) {
                Object.keys(play).forEach(p => {
                    filteredPlays[p] = 1
                })
            }
        })
    })
    const playbook = cloneDeep(playbooks[0])
    Object.keys(filteredPlays).forEach(p => {
        playbook.problemStatements.push(p)
    })
    dispatch(setPlayBook(playbook))
}

function Topic({ itemId, data }) {
    const dispatch = useDispatch();
    const ckm = useSelector(selectCkm)

    useEffect(() => {
        const selectedProductPhases = []
        Object.keys(ckm.topics).forEach(topicKey => {
            Object.keys(ckm.topics[topicKey]).forEach(itemKey => {
                const item = ckm.topics[topicKey][itemKey]
                if (item.selected) {
                    Object.keys(item.productPhases).forEach(ppKey => {
                        selectedProductPhases.push(ppKey)
                    })
                }
            })
        })

        const link = d3.selectAll('g path');
        const node = d3.selectAll('text');
        node.classed("node--selected1", n => false)
        node.classed("node--selected2", n => false)
        link.classed("link--selected", l => false)

        const assets = []
        selectedProductPhases.forEach(pp => {
            ckm.hedData.find(hd => hd.name === pp).imports.forEach(asset => {
                assets.push(asset)
            })
        })

        addPlays(dispatch, selectedProductPhases, assets, plays)
        if (selectedProductPhases.length == 0) {
            return;
        }

        link.classed("link--selected", l => {
            if (selectedProductPhases.includes(l.source.id) && assets.includes(l.target.id)) {
                return true;
            } else {
                return false;
            }
        }).filter(l => {
            if (selectedProductPhases.includes(l.source.id) && assets.includes(l.target.id)) {
                return true;
            } else {
                return false;
            }
        }).raise()

        node.each(function (d, i, nodes) {
            const selected = d3.select(this)
            const selectedId = d.id

            if (selectedProductPhases.includes(selectedId)) {
                selected.classed("node--selected1", n => true)
            }
            if (assets.includes(selectedId)) {
                selected.classed("node--selected2", n => true)
            }
            /*
            productPhases.forEach(ppKey => {
                if (ppKey === selectedId) {
                    console.log(`${ppKey} === ${selectedId}`)
                    selected.classed("node--selected", n => n.source)

                    //node.each(n => { n.target = n.source = false; });
                    link.classed("link--target", l => { if (l.target === d) return l.source.source = true; })
                        .classed("link--source", l => { if (l.source === d) return l.target.target = true; })
                        .filter(l => l.target === d || l.source === d)
                        .raise();
                    node.classed("node--selected", n => n.source)
                        .classed("node--selected", n => n.target)

                    //selected.dispatch('mouseover')
                }
            })
            //console.log(d)
            //console.log(nodes[0].__data__.id)
            //console.log(selected)
            //console.log(selected._groups[0])
            //console.log(selected._groups[0][0].__data__.id)
            */
        })

    }, [data.selected])

    const onItemClick = () => {
        dispatch(setTopicSelected(itemId))
    }

    const statusClassName = `topicItem ${data.selected ? 'tiSelected' : ''}`
    return (
        <button className={statusClassName} onClick={onItemClick}>{itemId.split('|')[1]}</button>
    )
}

export default Topic
